import { buildTestModeHeaders, isTestEnv } from '../testMode'

describe('testMode helpers', () => {
  const oldEnv = { ...process.env }
  afterEach(() => {
    process.env = { ...oldEnv }
  })

  it('returns header in test node env', () => {
    process.env.NODE_ENV = 'test'
    expect(buildTestModeHeaders()).toEqual({ 'X-Test-Mode': 'true' })
  })

  it('returns empty headers otherwise', () => {
    process.env.NODE_ENV = 'production'
    delete process.env.NEXT_PUBLIC_TEST_MODE
    expect(buildTestModeHeaders()).toEqual({})
  })
})
